<template>
  <div>
    <template>
      <div>
        <span style="float: left">默认查看1班------点击按钮查看其他班级</span>
        <el-button
          v-for="item in classList"
          :key="item"
          ref="changeColor"
          @click="mergeData(item)"
          >{{ "班级" + item }}</el-button
        >
      </div>
      <el-table
        :data="timetable"
        :span-method="objectSpanMethod"
        border
        ref="multiplerTable"
        :header-cell-style="{
          background: '#d9e5fd',
          color: 'black',
          fontWeight: 1000,
        }"
        :cell-style="tableCellStyle"
      >
        <el-table-column prop="sjd" label="时间段" width="80" align="center">
        </el-table-column>
        <el-table-column prop="section" label="节次" width="80" align="center">
        </el-table-column>
        <el-table-column prop="mon" label="星期一" align="center">
          <template slot-scope="scope">
            <h4>{{ scope.row.mon.course }}</h4>
            <div v-html="scope.row.mon.l_content"></div>
          </template>
        </el-table-column>
        <el-table-column prop="tue" label="星期二" align="center">
          <template slot-scope="scope">
            <h4>{{ scope.row.tue.course }}</h4>
            <div v-html="scope.row.tue.l_content"></div>
          </template>
        </el-table-column>
        <el-table-column prop="wed" label="星期三" align="center">
          <template slot-scope="scope">
            <h4>{{ scope.row.wed.course }}</h4>
            <div v-html="scope.row.wed.l_content"></div>
          </template>
        </el-table-column>
        <el-table-column prop="thu" label="星期四" align="center">
          <template slot-scope="scope">
            <h4>{{ scope.row.thu.course }}</h4>
            <div v-html="scope.row.thu.l_content"></div>
          </template>
        </el-table-column>
        <el-table-column prop="fri" label="星期五" align="center">
          <template slot-scope="scope">
            <h4>{{ scope.row.fri.course }}</h4>
            <div v-html="scope.row.fri.l_content"></div>
          </template>
        </el-table-column>
        <el-table-column prop="sat" label="星期六" align="center">
          <template slot-scope="scope">
            <h4>{{ scope.row.sat.course }}</h4>
            <div v-html="scope.row.sat.l_content"></div>
          </template>
        </el-table-column>
        <el-table-column prop="sun" label="星期天" align="center">
          <template slot-scope="scope">
            <h4>{{ scope.row.sun.course }}</h4>
            <div v-html="scope.row.sun.l_content"></div>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    // 下午节次数
    afternoonLength: {
      type: [String, Number],
      default: 4
    },
    // 总节次
    Length: {
      type: [String, Number],
      default: 12
    },
    // 课表数据
    events: {
      type: Array,
      default: null
    }
  },
  data () {
    return {
      // 课程表数据
      lessonList: [],
      classList: [],
      timetable: [],
      events1: [],
      newlet: [],
      selectByClass: '',
      weeks: ['mon', 'tue', 'wed', 'thu', 'fri', 'sat', 'sun']
    }
  },
  mounted () {
    this.mergeData(1)
  },
  watch: {
    events: {
      handler (newVal, oldVal) {
        this.mergeData(1)
      },
      deep: true
      // immediate: true
    }
  },
  created () {
    this.getAllLessonMsg()
    this.makeTimetable()
  },
  methods: {
    getAllLessonMsg () {
      this.axios.get('/api/getAll/lessonInfo').then((res) => {
        const array = res.data.list
        this.lessonList = res.data.list

        const m1 = new Map()
        const unique1 = (array, key) => {
          for (const item of array) {
            if (!m1.has(item[key])) {
              m1.set(item[key], item)
            }
          }
          return [...m1.values()]
        }
        const a1 = unique1(array, 'l_class')

        for (let i = 0; i < a1.length; i++) {
          this.classList.push(a1[i].l_class)
        }
        this.classList.sort()
      })
    },
    // 单元格添加背景色
    tableCellStyle (row) {
      if (row.row[row.column.property].l_title !== undefined) {
        return 'background-color:rgb(24 144 255 / 80%);color: #fff; border-radius:10px'
      }
    },
    // 构造课程表完整数据
    makeTimetable () {
      this.timetable = []
      for (let i = 0; i < this.Length; i++) {
        const one = {
          sjd: '',
          section: i + 1,
          mon: {},
          tue: {},
          wed: {},
          thu: {},
          fri: {},
          sat: {},
          sun: {}
        }
        if (i < 4) {
          one.sjd = '上午'
        } else if (i > 3 && i < this.afternoonLength + 4) {
          one.sjd = '下午'
        } else {
          one.sjd = '晚上'
        }
        this.timetable.push(one)
      }
    },
    async mergeData (item) {
      if (this.newlet !== null) {
        this.timetable = []
        this.makeTimetable()
        await this.axios.get('/api/getAll/lessonInfo').then((res) => {
          this.events1 = res.data.list
          const newData = []
          this.events1.forEach((e) => {
            if (Object.keys(newData).indexOf('' + e.l_class) === -1) {
              newData[e.l_class] = []
            }
            newData[e.l_class].push(e)
          })
          this.newlet = newData[item]
          // const newlet = newData[3]
          if (item !== null) {
            for (let i = 0; i < newData.length - 1; i++) {
              if (this.newlet[0].l_class !== undefined) {
                if (this.newlet.length > 0) {
                  for (let i = 0; i < this.newlet.length; i++) {
                    const week = this.weeks[this.newlet[i].l_day - 1]
                    this.timetable[this.newlet[i].jc.start - 1][week] =
                      this.newlet[i]
                  }
                }
              }
            }
          }
        })
      }
    },
    objectSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex < 4) {
          if (rowIndex === 0) {
            return {
              rowspan: 4,
              colspan: 1
            }
          } else {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        } else if (rowIndex > 3 && rowIndex < 4 + this.afternoonLength) {
          if (rowIndex === 4) {
            return {
              rowspan: this.afternoonLength,
              colspan: 1
            }
          } else {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        } else {
          if (rowIndex === 4 + this.afternoonLength) {
            return {
              rowspan: this.Length - 4 - this.afternoonLength,
              colspan: 1
            }
          } else {
            return {
              rowspan: 0,
              colspan: 0
            }
          }
        }
      }
      if (columnIndex === 2) {
        if (row.mon.l_title !== undefined) {
          return {
            rowspan: row.mon.jc.end - row.mon.jc.start + 1,
            colspan: 1
          }
        } else {
          return {
            rowspan: 1,
            colspan: 1
          }
        }
      }
      if (columnIndex === 3) {
        if (row.tue.l_title !== undefined) {
          return {
            rowspan: row.tue.jc.end - row.tue.jc.start + 1,
            colspan: 1
          }
        } else {
          return {
            rowspan: 1,
            colspan: 1
          }
        }
      }
      if (columnIndex === 4) {
        if (row.wed.l_title !== undefined) {
          return {
            rowspan: row.wed.jc.end - row.wed.jc.start + 1,
            colspan: 1
          }
        } else {
          return {
            rowspan: 1,
            colspan: 1
          }
        }
      }
      if (columnIndex === 5) {
        if (row.thu.l_title !== undefined) {
          return {
            rowspan: row.thu.jc.end - row.thu.jc.start + 1,
            colspan: 1
          }
        } else {
          return {
            rowspan: 1,
            colspan: 1
          }
        }
      }
      if (columnIndex === 6) {
        if (row.fri.l_title !== undefined) {
          return {
            rowspan: row.fri.jc.end - row.fri.jc.start + 1,
            colspan: 1
          }
        } else {
          return {
            rowspan: 1,
            colspan: 1
          }
        }
      }
      if (columnIndex === 7) {
        if (row.sat.l_title !== undefined) {
          return {
            rowspan: row.sat.jc.end - row.sat.jc.start + 1,
            colspan: 1
          }
        } else {
          return {
            rowspan: 1,
            colspan: 1
          }
        }
      }
      if (columnIndex === 8) {
        if (row.sun.l_title !== undefined) {
          return {
            rowspan: row.sun.jc.end - row.sun.jc.start + 1,
            colspan: 1
          }
        } else {
          return {
            rowspan: 1,
            colspan: 1
          }
        }
      }
    }
  }
}
</script>

<style scoped>
.active {
  background: red;
}
</style>
